
* {
            margin: 0;
            padding: 0;
            box-sizing: border-box;
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        }
        
        body {
            color: #333;
        }
        
        .container {
            max-width: 1290px;
            margin: 0 auto;
            padding: 0 20px;
        }
        
        /* Header */
        header {
            
            height: 80px;
            border-bottom: 1px solid #eee;
        }
        
        .header-container {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .logo {
            height: 75px;
        }
        
        .logo img {
            height: 75px;
        }
        
        nav {
            display: flex;
            gap: 30px;
        }
        
        nav a {
            text-decoration: none;
            color: #333;
            font-weight: 600;
            position: relative;
        }
        
        .dropdown::after {
            content: "▼";
            font-size: 8px;
            margin-left: 5px;
            position: relative;
            top: -2px;
        }
        
        .icons {
            display: flex;
            gap: 20px;
            align-items: center;
        }
        
        .icon {
            font-size: 20px;
            cursor: pointer;
        }
        
        /* Product Section */
        .product-section {
            display: flex;
            padding: 40px 0;
            gap: 30px;
        }
        
        .product-thumbnails {
            display: flex;
            flex-direction: column;
            gap: 15px;
            width: 80px;
        }
        
        .thumbnail {
            width: 90px;
            height: 120px;
            border: 1px solid #ddd;
            cursor: pointer;
            overflow: hidden;
        }
        
        .thumbnail img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        
        .thumbnail.active {
            border-color: #4CAF50;
        }
        
        .product-image-container {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        
        .product-main-image {
            width: 480px;
            height: 530px;
            background-color: #f9f7f2;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
        .product-main-image img {
            width: 480px;
            height: 530px;
        }
        
        .product-info {
            flex: 1;
            padding: 20px 0;
        }
        
        .product-title {
            font-size: 26px;
            font-weight: 600;
            margin-bottom: 20px;
            color: #333;
        }
        
        .product-price {
            font-size: 24px;
            font-weight: 700;
            margin-bottom: 20px;
            color: #b06e28;
        }
        
        .product-description {
            margin-bottom: 30px;
            line-height: 1.6;
            color: #555;
        }
        
        .product-features {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            border-top: 1px solid #eee;
            border-bottom: 1px solid #eee;
            padding: 20px 0;
            margin-bottom: 30px;
            gap: 10px 20px; /* Khoảng cách giữa các cột và hàng */
        }
        
        .feature {
            font-weight: 500;
        }
        
        
        /* Quantity Controls - Edited */
        .quantity-controls {
            display: flex;
            align-items: center;
            background-color: #4CAF50; /* Màu nền xanh lá cây */
            border-radius: 25px; /* Bo góc tròn */
            padding: 5px 15px; /* Khoảng cách bên trong */
            width: fit-content; /* Chiều rộng tự động theo nội dung */
        }
        
        .quantity-wrapper {
            display: flex;
            align-items: center;
            background-color: white;
            border-radius: 30px; /* Bo tròn nhẹ hơn nhưng hiện đại */
            
            border: 1px solid #ddd; /* Giữ viền nhẹ */
        }
        
        .quantity-btn {
            width: 30px;
            height: 30px;
            border: none;
            background: none; /* Bỏ nền riêng của nút */
            font-size: 18px;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #333;
        }
        
        .quantity-input {
            width: 20px;
            height: 30px;
            text-align: center;
            font-size: 16px;
            border: none;
            background: none; /* Bỏ nền của input */
            color: #333; /* Màu chữ đen */
            font-weight: 500;
        }
        
        .add-to-cart {
            background: none; /* Bỏ nền của nút */
            color: white; /* Màu chữ trắng */
            border: none;
            padding: 10px 20px;
            font-size: 16px;
            font-weight: 600;
            cursor: pointer;
        }
        
        .add-to-cart:hover {
            background: none; /* Không thay đổi nền khi hover */
            opacity: 0.9; /* Hiệu ứng mờ nhẹ khi hover */
        }
        
        .product-dots {
            display: flex;
            justify-content: center;
            gap: 8px;
            margin-top: 10px;
        }
        
        .dot {
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background-color: #ddd;
            cursor: pointer;
        }
        
        .dot.active {
            background-color: #333;
        }
        .product-info {
            margin-bottom: 30px;
            font-family: Arial, sans-serif;
            line-height: 1.6;
            color: #333;
            max-width: 800px;
            margin: 0 auto;
            padding: 20px;
        }

        .product-info h2 {
            font-size: 18px;
            margin-bottom: 10px;
            color: #333;
        }

        .product-info p {
            color: #666;
            margin-bottom: 15px;
        }
        .cr{
            background-color: #FAFBF7;
        }
        .customer-review {
            width: 1290px;
            margin: 0 auto;
          
            border-radius: 8px;
      
            padding: 30px;
        }

        h1 {
            font-size: 28px;
            font-weight: bold;
            margin-bottom: 20px;
            color: #333;
        }

        .rating-summary {
            display: flex;
            align-items: center;
            gap: 15px;
            margin-bottom: 25px;
        }

        .average-rating {
            font-size: 36px;
            font-weight: bold;
            color: #333;
        }

        .stars {
            color: #ffa500;
            font-size: 24px;
        }

        .review-count {
            font-size: 16px;
            color: #666;
        }

        .review {
            border-bottom: 1px solid #eee;
            padding: 20px 0;
        }

        .review:last-child {
            border-bottom: none;
        }

        .review h2 {
            font-size: 20px;
            font-weight: 600;
            color: #333;
            margin-bottom: 5px;
        }

        .review .stars {
            font-size: 20px;
            margin-bottom: 8px;
        }
        .review-date {
            display: block;
            font-size: 14px;
            color: #888;
            margin-bottom: 8px;
        }

        .comment {
            color: #555;
            font-size: 16px;
            margin-top: 5px;
        }

        .write-review {
            background-color: #8b5a2b;
            color: #fff;
            border: none;
            padding: 12px 25px;
            border-radius: 5px;
            cursor: pointer;
            font-size: 16px;
            font-weight: 500;
            transition: background-color 0.3s ease;
            display: block;
            margin-top: 20px;
        }

        .write-review:hover {
            background-color: #7a4a23;
        }
        .product-contact-section {
            background-color: #F9E5E2;
            text-align: center;
            padding: 60px 20px;
        }

        .contact-title {
            font-size: 24px;
            color: #333;
            margin-bottom: 20px;
            line-height: 1.4;
        }

        .contact-button {
            background-color: #8D5524;
            color: white;
            border: none;
            padding: 12px 24px;
            font-size: 16px;
            border-radius: 5px;
            cursor: pointer;
            transition: background-color 0.3s ease;
        }

        .contact-button:hover {
            background-color: #6D4C41;
        }
        .footer-container {
            max-width: 1200px;
            margin: 0 auto;
            padding: 2rem 1rem;
        }
        
        .footer-grid {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 2rem;
        }
        
        .footer-column-title {
            color: var(--primary-green);
            font-weight: 600;
            margin-bottom: 1rem;
            border-bottom: 2px solid var(--secondary-green);
            padding-bottom: 0.5rem;
        }
        
        .footer-links {
            list-style: none;
            padding: 0;
        }
        
        .footer-links li {
            margin-bottom: 0.5rem;
            transition: color 0.3s ease;
        }
        
        .footer-links li:hover {
            color: var(--primary-green);
            cursor: pointer;
        }
        
        .newsletter-section {
            background-color: white;
            padding: 1.5rem;
            border-radius: 8px;
            box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        }
        
        .newsletter-input {
            width: 100%;
            padding: 0.75rem;
            border: 1px solid var(--primary-green);
            border-radius: 4px;
            margin-bottom: 1rem;
        }
        
        .newsletter-button {
            width: 100%;
            padding: 0.75rem;
            background-color: var(--primary-green);
            color: white;
            border: none;
            border-radius: 4px;
            cursor: pointer;
            transition: background-color 0.3s ease;
        }
        
        .newsletter-button:hover {
            background-color: var(--secondary-green);
        }
        
        .payment-methods {
            display: flex;
            justify-content: center;
            gap: 1rem;
            margin-top: 2rem;
            padding: 1rem;
            background-color: white;
            border-top: 1px solid #e5e7eb;
        }
        
        .payment-methods img {
            max-height: 40px;
            filter: grayscale(20%);
            transition: transform 0.3s ease;
        }
        
        .payment-methods img:hover {
            transform: scale(1.1);
        }
        
        .copyright {
            text-align: center;
            color: var(--text-gray);
            padding: 1rem;
            font-size: 0.875rem;
        }
        
        @media (max-width: 768px) {
            .footer-grid {
                grid-template-columns: 1fr;
            }
        
            .payment-methods {
                flex-wrap: wrap;
            }
        }
